/* block-media */
.block-media {
    display: block;
    width: 100%; 
    margin-bottom: 30px;
    transition: all ease .3s;
    &:hover { 
        .cover-image {
            img {
                transform :rotate(3deg) scale(1.15);
            }
        }
    }
    .cover-image {
        display: block;
        width: 100%;
        overflow: hidden;
        img {
            transition: all ease .3s;
        }
    }
    .title {
        display: block;
        font-size: 1.125rem;
        text-transform: uppercase;
        margin-top: 20px;
        margin-bottom: 10px;
        font-weight: 600;
        max-width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        &, a {
            color: @primary-color;
        }
    }
    .block-content {
        padding-right: 30px;
    }
    .readmore {
        font-weight: bold;
        margin: 10px auto 15px;
        display: inline-block;
        color: @second-color;
        position: relative;
        .readmore-icon {
            display: inline-block;
            width: 0px;
            overflow: hidden;
            transition: all ease .3s;
        }
        &:hover {
            .readmore-icon { width: 15px; }
        }
        &::before {
            content: ' ';
            position: absolute;
            top: auto;
            bottom: 0;
            width: 100%;
            left: 0;
            right: 0;
            height: 2px;
            background: @second-color;
        }
        &.button-border {
            padding: 4px 15px;
            border: 2px solid @primary-color;
            color: @primary-color;
                text-transform: uppercase;
            &::before {
                display: none;
            }
            &:hover {
                border: 2px solid @second-color;
                color:#fff;
                background-color: @second-color;
            }
        }
    }
}

.block-media-right {
    .block-media;    
    box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.08);
    &:hover {
        box-shadow: 0px 3px 6px 0 rgba(0, 0, 0, 0.3);
    }
}
.block-media-top {
    .block-media;    
    // box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.08);
    .title {
        &, a {
            color: #444;
        }
    }
    &:hover {
        box-shadow: 0px 3px 6px 0 rgba(0, 0, 0, 0.3);
    }
    .block-content {
        padding-right: 30px;
        padding-left: 30px;
    }
}
.block-media-product {
    .block-media;    
    box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.08);
    border-bottom: 3px solid @primary-color;
    &:hover {
        box-shadow: 0px 3px 6px 0 rgba(0, 0, 0, 0.3); 
        .cover-image {
            img {
                transform :rotate(0deg) scale(1.15);
            }
        } 
    }
    .block-content {
        padding-right: 30px;
        padding-left: 30px;
    }
}
.block-media-right-radius {
    .block-media;
    
    .cover-image {
        border-radius: 0px 0px 30px 0px;   
        box-shadow: -4px 4px 5px 0px rgba(0, 0, 0, 0.3); 
    }
    &:hover .cover-image{
        box-shadow: 0px 0px 6px 0 rgba(0, 0, 0, 0.3);
    }
}

.btn-cart-in {
    display: block;
    ul {
        padding: 0;
        margin: 0;
        list-style: none;
        li {
            display: inline-block;
        }
    }
    .button {
        background: #ccc;
        border-radius: 100%;
        color: #fff;
        font-size: 20px;
        padding: 3px 0;
        display: inline-block;
        width: 35px;
        height: 35px;
        white-space: nowrap;
        text-align: center;
        line-height: 35px;
        margin: 10px;
        margin-bottom: 20px;
        transition: all ease .3s;
        &.bg-red:hover {
            background-color: #ef1b27;
        }
        &.bg-yellow:hover {
            background-color: @second-color;
        }
    }
}

 /* TRACKING FORM */
 .tracking_form {
    label {
        font-weight: bold;
        color: #65a0b0;
        white-space: nowrap;
        max-width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        margin-bottom: 20px;
        font-size: large;
    }
     .relative-panel {
         position: relative;
     }
     .btn-submit {
         position: absolute;
         right: 15px;
         bottom: 0;
     }
 }

  /* TRACKING PANEL */
  .tracking-panel {
      min-height: 60vh;
  }
  .tracking-item-wrapper {
      display: block;
      width: 100%;
  }
  .tracking-item  {
      width: 435px;
      max-width: 100%;
      padding-bottom: 305/435*(435)/295 * 100%;
      position: relative;
      height: 0;
      background: #ccc url(../images/bg_tracking_item.jpg) repeat center top / (435 - 60)/295*100% 90%;
      margin-bottom: 1px;
      height: 0;
      overflow: hidden;
      .tracking-item-img {
            position: absolute;
            top: 0;
            right: 0;
            left: 0; 
            width: 295/435*100%;
            margin: auto;
      }
      .tracking-item-content {
            position: absolute;
            top: auto;
            bottom: 0;
            right: 0;
            left: 0; 
            width: 295/435*100%;
            height: 90px;
            margin: auto;
            border: 2px dashed @primary-color;
            border-radius: 4px;
            padding: 15px;
            text-align: center;
            display: flex;
            align-items: center;
            justify-content: center;
      }
      &.active {
        .tracking-item-content {
            border: 2px dashed @second-color;
            color:  @second-color;
            font-weight: 500;
        }
      }
  }

  /* PRODUCT DETAIL POPUP */
  .modal-content { 
    border-radius: 1rem;
}
  @media (min-width: 576px) {
    .modal-dialog { 
        margin: 5.75rem auto;
    }
  }
  
  .item-border {
      display: block;
      border: 1px solid #eaeaea;
  }
  .thumb-carousel {
    .current .item {
        border: 2px solid @primary-color;
    }
  }

  .product-content {
    .title {
        font-size: large;
        font-weight: 500;
        color: @second-color;
        margin-bottom: 10px;
    }
  }
  .product-info {
      margin-bottom: 15px;
      >div {
          display: block;
          padding: 6px 0;
      }
      .meta-attributor {
          font-weight: bold;
      }
  }

  /* GALLARY */
  .section-gellary {
    background: #2196f3;
    padding: 30px;
    .thumb-panel {
        padding: 15px;
        background: fade(#fff, 60);
        margin-top: 15px;
    }
}
/* LOGIN PAGE */
.section-cover {
    min-height: 80vh;
    background: darken(@primary-color, 30) url(../images/bg_login.jpg) repeat center top/ 100% 100%;
}
.cover-image-form {
    position: relative;
    min-height: 320px;
    background: darken(@primary-color, 10) url(../images/bg_register.jpg) repeat center top/ 100% 100%;
}
.form-panel-layout {
    max-width: 100%;
    display: block;
    margin: auto;
    overflow: hidden;
    background: #fff;
    box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.08);
    border-radius: 10px;
}
.login-panel, .register-panel {
    display: block;
    max-width: 100%;
    min-height: 120px;
    padding: 60px 30px 0;
    text-align: center;
    transition: all ease .3s;
    .title {
        font-size: x-large;
        text-indent: -1px;
        color: @second-color;
        font-weight: 700;
        letter-spacing: -1.4px;
    }
    .icon-form-label {
        background: @primary-color;
        color: #fff;
        width: 30px;
        max-width: 30px;
        height: 30px;
        display: inline-block;
        border-radius: 100%;
        text-align: center;
        overflow: hidden;
        padding: 0;
        line-height: 30px;
    }
    .helper-link, label {
        font-size: small;
        color: @primary-color;
        vertical-align: middle;
        +a {
            &:hover {
                color: @second-color;
            }
        }
    }
    .helper-link {
        padding: 20px 0;
        display: inline-block;
    }
    // custom style 
    .form {
        margin: 50px 0;
    }
    .form-group {
        margin: 15px 0 30px;
    }
    .btn {
        border-radius: 20px;
        min-width: 220px;
        font-weight: 400;
    }
}

/* menubar-wrapper */
.menubar-wrapper {
    background: @second-color;
    ul {
        padding: 0;
        margin: 0;
        list-style: none;
        >li {
            display: inline-block;
            > a, >span {
                color: #fff;
                display: block;
                padding: 6px 12px;
                text-transform: uppercase;
                font-weight: bold;
                font-size: large;
                &:hover {
                    color: @primary-color;
                }
            }
        }
    }
}

.support-float {
    white-space: nowrap;
    color: #fff; 
    margin: 15px 0 5px;
    padding-left: 65px;
    position: relative; 
    a:hover { color: @primary-color; }
    i {
        position: absolute;
        left: 15px;
        top:0;
        display: block;
        width: 50px;
        height: 50px;
        line-height: 50px;
        text-align: center;
        font-size: 50px;
        border-radius: 3px;
        float: left;
    }
    h1,h2,h3,h4,h5,h6 {
        font-size: .95rem;
        font-weight: 700;
        letter-spacing: -.04em;
    }
    .hotline {
        font-weight: bold;    color: #3c3c3c;
    }
}

/* SHOP CART */
.section-shopcart-page {
    min-height: 80vh;
}
.shopcart-panel {
    .form-panel-layout;
    padding-top: 0;
    border: 1px solid #eaeaea;
    .block-title {
        margin: 0;
        display: block;
        background: @primary-color;
        .title { margin: 0; color: #fff; padding: 15px; }
    }
    .table thead th {
        color: @primary-color;
    }
    .btn { 
        text-transform: none;
        border-radius: 20px;
    }
}

.button-bordered {
    display: inline-block;
    padding: 15px 30px;
    text-transform: uppercase;
    text-align: center;
    border: 2px solid @primary-color;
    border-radius: 4px;
    margin-top: 15px;
    font-weight: 600;
    transition: all ease .3s;
    i {
        margin-left: 0px;
        transition: all ease .3s;
    }
    &:hover {
        background-color: @primary-color;
        border: 2px solid @primary-color;
        color: #fff;
        i {
            margin-left: 15px;
        }
    }
}
