// Toggle switch
@switchWidth: 60px;
@switchHeight: 28px;
.toggle-switch {
    padding: 0;
    margin: 0;
    input.switch-toggle {
        display: block;
        position: absolute;
        top: 9px;
        left: 7px;
        z-index: 1;
        opacity: 0;
    }
    .switch-viewport {
        color: #fff;
        white-space: nowrap;
        overflow: hidden;
        background: @primary-color;
        border-radius: 30px;
        height: @switchHeight;
        width: @switchWidth * 2;
        position: relative;
        margin-top: 15px;
    }
    .switch-button,
    .switch-content  {
        transition: all 500ms ease-in-out;
        width: @switchWidth;
        display: inline-block;
        height: @switchHeight - 4;
        background: transparent;
        margin-top: 2px;
        font-size: x-small;
        text-align: center;
        line-height: @switchHeight - 2;
        font-weight: 700;
        color: @primary-color;
        border-radius: 50px;
        z-index: 2;
        position: relative;
        // left: 2px;
        // right: auto;
    }
    .switch-button {
        background-color: #fff;
        position: absolute;
        left: 2px;
        right: auto;
        z-index: 1;
    }
}
.toggle-switch input.switch-toggle:checked + .switch-viewport .switch-slider .switch-button{
    left: @switchWidth - 2;
} 
