        color: #fff;
        font-size: 50px;
        font-weight: 700;
    }
    .sevices-title-nomarl,
    .block-title .title.sevices-title-nomarl{
        margin: 10px 0 15px;
        color: #f5b353 !important;
        text-shadow: 1px 1px #00000047;
        font-size: 50px;
        font-weight: 500;
        font-size: 36px;
    }
}
.block-view-more {
    line-height: 48px;
    padding: 0 15px;
    i  {
        font-size: x-small;
    }
}
.view-more {
    color: @primary-color;
    font-size: 12px;
    font-weight: bold;
    display: inline-block;
    padding: 0px 10px; 
}
/* FOOTER */

.copyright {
    border-top: 1px solid #dcdcdc;
    padding: 10px 0;
    font-size: small;
    color: #999999;
}
footer, .footer {
    padding-top: 70px;
    &, a {
        color: #666;
    }
    a{
        &:hover {
            color: #ccc;
        }
    }
    .social-icon {
        background: #666;
        border-radius: 100%;
        overflow: hidden;
        cursor: pointer;
        &, a {
            color: #fff;
        }
        i {
            padding: 4px 8px;
            width: 36px;
            font-size: 18px;
            line-height: 28px;
            text-align: center;
        }
    }
    .f-logo {
        float: left;
        margin-right: 10px;
        max-width: 100px;
    }
    .f-slogan {
        display: inline-block;
        padding: 10px;
        font-size: 14px;
        letter-spacing: -1px;
        padding-top: 20px;
    }
    ul {
        list-style: none;
        padding: 0;
        margin: 0;
        margin-bottom: 15px;
        li {
            >span, >a {
                padding: 10px 0px;
                display: block; 
                font-size: inherit;
                i.material-icons.list-style {
                    font-size: 6px;
                    line-height: 20px;
                    display: inline-block;
                    margin: 0 10px 0px 0;
                    vertical-align: top;
                }
            }
            &:last-child {
                >span, >a {
                    border-bottom: none;
                }
            }
        }
    }
    .title {
        font-size: large;
        font-weight: 500;
        line-height: 1.05;
        text-transform: uppercase;
        position: relative;
        margin: 10px 0 15px;
        padding-bottom: 10px;
        position: relative;
        &.divider-bottom {
            &::before {
                content: ' ';
                height: 2px;
                background: #8a8a8a8a;
                position: absolute;
                bottom: 0;
                left: 0;
                width: 40px;
            }
        }
    }
    &.dark {
        background: darken(@primary-color, 0);
        border-top: 2px solid @second-color;
        &,a {
            color: #e8e8e8;
        }
        .divider-bottom {
            &::before { background: #fff; }
        }
        .social-icon {
            background: #fff;
            border-radius: 100%;
            overflow: hidden;
            cursor: pointer;
            &, a {
                color: @primary-color;
                &:hover {
                    color: #fff;
                }
            }
        }
        .copyright {
            border-top: 1px solid #dcdcdc33;
            color: #97c0cc;
        }
    }
}


/* SUB PAGE */

.subpage-cover {
    padding-bottom: 200px;
    position: relative;
    height: 0;
    overflow: hidden;
    background: #2d71a2;
    text-align: center;
    .page-title {
        position: absolute;
        left: 00;
        right: 0;
        top: 0;
        bottom: 0;
        margin-top: 0;
        color:#fff;
        max-width: 480px;
        margin:auto;
        padding: 15px;
        >span {
            &:hover {
                color: #fff;
            }
            &::before {
                content: ' ';
                background: #ffffff;
            }
        }
    }
}
.section-subpage {
    margin: 30px 0;
    .block-title {
        margin: 40px 0;
    }
}
.subpage { 
    position: relative;
    z-index: 9999;
}
.page-title {
    margin-bottom: 0;
}
.page-title, .module-title {
    margin-top: 15px;
    font-size: medium;
    font-weight: 500; 
    text-transform: uppercase;
    // border-bottom: 1px solid #eaeaea;
    &, a {
        color: @primary-color;
    }
    >a, span {
        display: inline-block;
        padding: 6px 0;
        position: relative;
        &:hover {
            text-decoration: none;
            color: @primary-color;
        }
        &::before {
            content: ' ';
            display: block;
            position: absolute;
            bottom: -1px;
            left: 0;
            width: 100%;
            height: 2px;
            background: @second-color;
        }
    }
}

.banner-add {
    width: 100%;
    display: block;
    margin-bottom: 15px;
    img {
        width: 100%;
    }
    &.sticky-top {
        padding-top: 50px;
    }
}
.div-padding-stiky {
    margin-bottom: -50px;
}


/* WIDGET BLOCK */
.widget-block {
    text-align: left;
    margin: 5px 0;
    .block-title {
        display: block;
        margin: 0;
        > span  {
            display: block;
            padding: 2px 8px;
            font-weight: bold;
            font-size: small;
        }
    }
    .block-content {
        max-height: 60px;
        overflow: hidden;
        border-top: 1px solid #eaeaea;
        padding: 5px 0px;
        ul {
            margin: 0;
            padding: 0;
            list-style: none;
            >li {
                display: inline-block;
                overflow: hidden;
                border-bottom: 1px solid #eaeaea;
                > span, > a {
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    display: inline-block;
                    padding: 1px 12px 0 0;
                    max-width: 100%;
                    overflow: hidden;
                }
            }
        }
    }
    @media (max-width: 768px) {
        .block-content {
            max-height: 160px;
        }
        &.vn-hnx-block {
            .block-content ul > li {
                &, > span { display: block; }
            }
            img {
                width: 100%
            }
        }
    }
}
/* HEAD NEW BLOCK */
.widget-block.head-news-block {
    .date {
        color: @second-color;
        min-width: 50px;
        display: inline-block;
    }
    .block-title {
        display: inline-block;
        > span  {
            background: @second-color;
            color: #fff;
        }
    }
    .block-content {
        ul {
            >li {
                width: 50%;
                float: left;
                max-height: 25px;
                line-height: 24px;
            }
        }
    }
    @media (max-width: 768px) {
        .block-content {
            ul {
                >li {
                    width: 100%;
                }
            }
        }
    }
}
/* Feature Box */
.feature-box-layout {
    padding: 50px 0;
}
.feature-box {
	display: -webkit-box;
	display: -ms-flexbox;
    display: flex;
    .feature-box-icon {
        display: -webkit-inline-box;
        display: -ms-inline-flexbox;
        display: inline-flex;
        -webkit-box-align: center;
        -ms-flex-align: center;
        // align-items: center;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
        -webkit-box-flex: 1;
        -ms-flex: 1 0 auto;
        flex: 1 0 auto; 
        color: #FFF; 
        position: relative;
        top: -3px;
        text-align: center; 
    }
    .feature-box-info {
        -webkit-box-flex: 1;
        -ms-flex: 1 1 100%;
        flex: 1 1 100%;
        padding-left: 15px;
    } 
    .feature-title {
        font-weight: 500!important;
        text-transform: uppercase;
        color: @primary-color;
        font-size: large;
        text-align: justify;
        margin-bottom: 10px;
    }
    p {
        color: #777;
        line-height: 1.6;
        margin: 0 0 20px;
    }
}


/* ICONS*/
.icons {
    background-color: transparent;
    background-image: url(../images/icons.png);
    width: 60px;
    height: 60px;
    display: inline-block;
    vertical-align: middle;
    &.icon-mission {
        background-position: 0 0;
    }
    &.icon-diamond {
        background-position: 0 60px;
    }
    &.icon-share {
        background-position: 0 120px;
    }
}
.icon-xs {
    background-color: #ccc;
    width: 24px;
    height: 18px;
    display: inline-block;
    vertical-align: middle;
}

/* Contact section */
.section-contact {
    padding: 30px 0;
    background: darken(@primary-color, 20) url(../images/footer-contact.png) repeat center top / auto 100%;
    .block-title .title , .subtitle {
        color: #fff;
        font-size: large;
    }
    .subtitle {
        margin: 0px 0 25px;
    }
    @media (min-width: 768px) {
        .block-title, .title , .subtitle {
            display: block;
            font-size: large;
            text-align: left;
        }
        .subtitle {
            font-size: x-large;
        }
    }
}

/* FORM */
.btn.btn-primary {
    background: @primary-color;
    color: #fff;
    &:hover, &:focus {
        background: fade(@primary-color, 50);
        color: #fff;
    }
}
.btn.btn-secondary {
    background: @second-color;
    color: #fff;
    &:hover, &:focus {
        background: fade(@second-color, 50);
        color: #fff;
    }
}
.custom-form {
    .btn-submit {
        min-width: 120px;
    }
    .form-control {
        border: 1px solid #eaeaea;
        border-radius: 5px;
        line-height: 30px;
        padding: 6px 12px;
        background: no-repeat bottom,50% calc(100% - 1px);
        background-color: #fff;
    }
    .btn.btn-primary {
        background: @primary-color;
        color: #fff;
    }
    &.light-text {
        ::placeholder {
            color:#fff;
        }
        .btn.btn-primary {
            background: @second-color;
            color: #fff;
        }
    }
}
/* Media post */
.media-caption {
    background: fade(#000, 98);
    padding: 15px;
    color: #fff;
    h2,h3,h4,h5,h6 {
        font-size: medium;
    }
}

.table-livi-data {
    border : 1px solid #eaeaea;
    .table-striped tbody tr:nth-of-type(odd) {
        background-color: rgba(255, 208, 142, 0.12);
    }
}