.partners {
    padding: 60px 0;
    background: transparent;
}
.owl-carousel .owl-item .partner-item {
    text-align: center; 
    display: block;
    img {
        max-width: 100%; 
        display: block;
        margin: auto;
        -webkit-filter: grayscale(1);
        filter: grayscale(1);
    }
    &:hover {
        img {            
            -webkit-filter: grayscale(0);
            filter: grayscale(0);
        }
    }
}