@services-hover-color: #f7f8fa;
.pro-services-carousel.owl-carousel {
    .owl-stage-outer { 
        overflow: visible;
    }
    .owl-item {
        opacity: .5;
        &.active {
            opacity: 1;
        }
    }
    .owl-dots {
        position: absolute;
        top: -60px;
        right: 0;
    }
}
.slide-posts {
    // padding-top: 10px;
    padding: 30px 0;
    img {
        width: 100%;
    }
     .block {
        position: relative;
        margin-bottom: 15px; 
        transition: all ease .3s;
        // border: 1px solid #eaeaea;
        // border-radius: 2px;
        background: #fff;
        box-shadow: 0px 2px 6px 0px fade(#000, 8);
        padding: 0px 15px 15px; 
        &.gray-block {  
            background-color: #fff;
            margin-bottom:30px;
            & {
                color: #666;
            }
            .title {
                &, a {     
                    color: #222;
                    text-transform: uppercase;
                    font-size: smaller;
                    font-weight: 500;
                    height: 3.6em;
                    display: block;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    max-width: 100%;
                    line-height: 1.6;
                    margin-bottom: 5px;
                }
            }
            p {
                font-size: .85em;
                color: #555;
                height: 2.4em;
                line-height: 1.4;
                display: block;
                text-overflow: ellipsis;
                overflow: hidden;
                max-width: 100%;
            }
            .readmore {
                display: inline-block;
                text-transform: uppercase;
                font-size: smaller;
                color: @primary-color;
                font-weight: 600;
                border: 1px solid transparent;
                padding: 4px 0px;
                margin: 5px 0 5px;
                border-radius: 1px;
                transition:  all ease .3s;
                &:hover {
                    background-color: @primary-color;
                    color: #fff;
                    padding: 4px 12px;
                }
            }
            .float-button {
                background: #e8eaec;
                color:  @primary-color;
            }
            &.active, &:hover {
                // background-color: @services-hover-color;
                // transform: translateY(-5px);
                &, a, .title {
                    // color: #fff;
                    transition:  all ease .3s;
                }
                box-shadow: 0px 3px 6px 0 rgba(0, 0, 0, 0.3);
            }
        }
        .cover-icon {
            padding: 30px;
            margin: auto;
            max-width: 160px;
        }
        .cover-image {
            padding: 0;
            margin: 0px -15px 15px;
            max-width: calc(100% + 30px);
            background: #f5f5f5;
            border-radius: 0;
            overflow: hidden;
        }
        .team-avatar{
            padding: 0;
            // margin: 15px auto;
            width: 70px;
            height: 70px;
            background: #ccc;
            border-radius: 100%;
            overflow: hidden;
            box-shadow: 0px 2px 4px 0 rgba(0, 0, 0, 0.3);
            border: 2px solid #fff;
            transition: all ease .3s;
            display: inline-block;
            float: left;
        }
        .title {
            &, h2,h3,h4 {
                font-size: 15px;
                font-weight: 500;
                // margin-bottom: 0;
                // text-transform: uppercase;
            }
        }
        .quote-block {
            .clearfix;
            display: block;
            width: 100%;
            margin-top: 32px;
            text-align: left;
            font-style: italic;
            padding: 15px 0 0;
            font-size: 15px; 
        }
        .hover-block {
            opacity: 0;
            transition: all ease .3s;
            margin-top: -40px;
            overflow: hidden;
        }
        &:hover {
            // .team-avatar{
            //     width: 85px;
            //     height: 85px;
            // }
            // .hover-block {
            //     opacity: 1;
            //     margin-top: 0;
            // }
        }
        .float-button {
            position: absolute;
            right: 30px;
            bottom: 0;
            transform: translateY(50%);
            &.btn:hover {
                box-shadow: 0px 6px 12px 0 rgba(0, 0, 0, 0.3);
            }
        } 
     }
}
