.service-blog {    
    display: block;       
    background: fade(#fff, 0); 
    cursor: pointer;    
    border-radius: 4px;
    padding: 15px;
    transition: all ease .3s;
    &:hover {        
        background: fade(#fff, 3);
        box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.08); 
    }
    .cover-icon {
        background: #ffffff08;
        border-radius: 100%;
        height: 200px;
        padding-top: 35px;
        position: relative;
        max-width: 200px;
        margin: auto;
        margin-top: 30px;
        [class^="flaticon-"]:before, [class*=" flaticon-"]:before, [class^="flaticon-"]:after, [class*=" flaticon-"]:after {
            font-family: Flaticon;
            font-size: 90px;
            font-style: normal;
            margin-left: 0;
            color: #fff;
        }
        &::before {
            content: ' ';
            position: absolute;
            left: 8px;
            right: 8px;
            top: 8px;
            bottom: 8px;
            border-radius: 100%;
            border: 1px solid #fff00;
            display: block;
            height: ~'calc( 100% - 16px)';
            transition: all ease .3s;
        }
        &:hover,&:focus, &:active {
            background: #fff;
            transform: scale(1.1);
            [class^="flaticon-"]:before, [class*=" flaticon-"]:before, [class^="flaticon-"]:after, [class*=" flaticon-"]:after  {
                color: @primary-color;
            }
            &::before {
                transform: rotateY(180deg);                
                border: 1px solid @primary-color;
            }
        }
    }
    p{
        color:fade(#fff, 80);
        font-size: small;
        max-width: 85%;
        text-align: center;
        margin: auto;
    }
    .readmore {
        display: block;
        margin: 15px 0;
        color: #fff;
        text-transform: uppercase;
        font-weight: 500;
        font-size: small;
    }
    .title a,
    .title span {
        color: #fff;
        font-size: large;
        padding-top: 15px;
        padding-left: 50px;
        display: block;
        text-align: center;
        max-width: ~'calc(100% - 50px)';
    }
    .cover-icon {
        transition: all ease .3s;
        img {
            max-width: 70px;
        }
    }
}
