/* SLIDE OWL */
.slide-owl-carousel .owl-dots {
    margin-top: -36px;
    text-align: right;
    padding-right: 64px;
}
.slide-owl-carousel.owl-carousel {
    .owl-nav button.owl-prev, .owl-nav button.owl-next {
        top: ~'calc(100% - 36px)'; 
        font-size: 12px;
        line-height: 30px;
        width: 30px;
        height: 30px;
        color: #353535;
        background: #ececec;
        border-radius: 0;
        box-shadow: none;
        transform: none;
        left: auto;
    }
    .owl-nav button.owl-next {
        right: -5px;
    }
    .owl-nav button.owl-prev {
        right: 26px;
    }
}

/* NEWS POST */
.news-post .news-post-image-link {
    width: 100%; 
}
.home {
    .news-post {
        background: #fff;
        box-shadow: @boxShadow;
        padding: 0;
        margin-bottom: 30px;
        .news-post-content {
            padding: 15px;
        }
        .title a {
            color: #222;
            text-transform: uppercase;
            font-size: smaller;
            font-weight: 500;
            height: 3.6em;
            display: block;
            text-overflow: ellipsis;
            overflow: hidden;
            max-width: 100%;
            line-height: 1.6;
            margin-bottom: 5px;
        }
        .post-desc {
            font-size: .85em;
            color: #555;
            height: 2.4em;
            line-height: 1.4;
            display: block;
            text-overflow: ellipsis;
            overflow: hidden;
            max-width: 100%;
        }
        .read-more-button {
            display: inline-block;
            text-transform: uppercase;
            font-size: smaller;
            color: #095892;    font-style: normal;
            font-weight: 600;
            border: 1px solid transparent;
            padding: 4px 0px;
            margin: 5px 0 5px;
            border-radius: 1px;
            transition: all ease .3s;
        }
    }
    .image-container {
        margin: 0;
    }
}

@media (min-width: 768px){
    .home .news-post .news-post-image-link {
        max-width: 100%;
    }
    .grid-news {
        .news-post .news-post-image-link {
            width: 50%;
            img {
                width: 100%;
            }
            position: relative;
            &::after {
                content: " ";
                position: absolute;
                top: 50%;
                right: 0;
                transform: translate(0%, -50%); 
                width: 0; 
                height: 0; 
                border-top: 20px solid transparent;
                border-bottom: 20px solid transparent;
                border-right: 15px solid #fff;
                border-left: none;
                z-index: 1;
            }
        }
        &:nth-child(3n), &:nth-child(4n) { 
            .news-post .news-post-image-link {
                float: right;
                margin-left: 15px;
                margin-right: 0;
                &::after {
                    content: " ";
                    position: absolute;
                    top: 50%;
                    right: auto;
                    left: 0;
                    transform: translate(0%, -50%); 
                    width: 0; 
                    height: 0; 
                    border-top: 20px solid transparent;
                    border-bottom: 20px solid transparent;
                    border-right: none;
                    border-left: 15px solid #fff;
                }
            }
        }
    }
}

